body {
  background-color:white
;/*rgb(224,224,224);*/
  font-family:arial,sans-serif;
}

.A{
  background-color:black;
  color:white;
}
.Heading {
  text-align: center;
  color:grey;
  padding: 30px;
  font-size: 40px;
}

.smallheading{
  margin-top: 20px;
  margin-bottom: 10px;
  color:black;
  font-size: 30px;
  padding: 15px;
  display: flex;
  flex-direction: column wrap;
  justify-content: relative;
}
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: black;
  opacity: 0.7; /* Firefox */
}

.field {
  margin: 20px;
  width: 26%;
  color:black;
  height: 27px;
  border-color: rgb(192,192,192);
  border-width: 0px;
  border-radius: 5px;
  position: relative;
  transition: 0.3s all;
  background-color: rgb(220, 220, 220);

  font-size: 15px;
  text-align: center;
}
.field:hover {
  background-color: white;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.05);
  color:black;
}

.button {
  margin:20px;
  width: 25%;
  height: 30px;
  border-color: rgb(192,192,192);
  border-width: 0px;
  border-radius: 5px;
  position: relative;
  transition: 0.3s all;
  background-color: white;
  color:black;
  font-size: 14px;
  text-align: center;
}
.button:hover{
  background-color: rgb(136,136,136);
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.05);
  color:black;
}


.Input_wrapper{
  margin-top: 20px;

  display: flex;
  flex-direction: row wrap;
  justify-content: relative;
  text-decoration-color: black;
}
.display{
  border-width: 10px;
}
